
.color-picker__outer {
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -ms-transition: all .5s;
  transition: all .5s;
}
.color-picker {
  height: 36px;
  width: 100%;
}
.color-picker.color-picker--small {
    height: 30px;
}
.color-picker__picker {
  position: relative;
  float: left;
  height: 100%;
  width: 56px;
  padding: 7px 2px;
  border: 1px solid #8391a5;
  border-radius: 2px;
  box-shadow: 0 2px 6px transparent;
  -webkit-transition: all .3s;
  -moz-transition: all .3s;
  -ms-transition: all .3s;
  transition: all .3s;
}
.color-picker__picker:hover {
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}
.color-picker__picker .color-picker__picker-inner {
    display: block;
    margin-right: 20px;
    height: 20px;
    background-color: #fff;
}
.color-picker__picker .el-input__icon {
    width: 20px;
}
.color-picker__base {
  margin-left: 58px;
  line-height: 1;
  cursor: pointer;
}
.color-picker__base .color-picker__base-item {
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 2px;
    background-color: #f0f;
    vertical-align: top;
    margin: 1px;
    border: 1px solid #8391a5;
    box-shadow: 0 0px 4px transparent;
    -webkit-transform: scale(1, 1);
    -ms-transform: scale(1, 1);
    -moz-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    -ms-transition: all .2s;
    transition: all .2s;
}
.color-picker__base .color-picker__base-item:hover {
      box-shadow: 0 0 4px rgba(0, 0, 0, 0.4);
      z-index: 10;
      -webkit-transform: scale(1.1, 1.1);
      -ms-transform: scale(1.1, 1.1);
      -moz-transform: scale(1.1, 1.1);
      transform: scale(1.1, 1.1);
}
.color-picker--small .color-picker__picker {
  padding: 4px 2px;
  width: 50px;
}
.color-picker--small .color-picker__base {
  margin-left: 52px;
}



