
.c-input-number {
  position: relative;
}
.c-input__inner-input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #fff;
  border-radius: 2px;
  border: 1px solid #bfcbd9;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  line-height: 1;
  outline: 0;
  padding: 3px 10px;
  box-sizing: border-box;
  background-image: none;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.c-input__ctrl {
  position: absolute;
  top: 1px;
  bottom: 1px;
  right: 2px;
  color: #8492A6;
  width: 14px;
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .25s;
  -moz-transition: all .25s;
  -ms-transition: all .25s;
  transition: all .25s;
}
.c-input__ctrl .c-input__ctrl-inc, .c-input__ctrl .c-input__ctrl-dec {
    position: relative;
    height: 50%;
    -webkit-transition: all .25s;
    -moz-transition: all .25s;
    -ms-transition: all .25s;
    transition: all .25s;
}
.c-input__ctrl .c-input__ctrl-inc:hover, .c-input__ctrl .c-input__ctrl-dec:hover {
      background-color: #E5E9F2;
}
.c-input-number:hover .c-input__ctrl {
  opacity: 1;
  visibility: visible;
}
.c-input__inner .c-input__inner-input {
  display: inline-block;
  width: 100%;
}
.c-input-number.small .c-input__inner-input {
  height: 30px;
}
.triangle {
  position: absolute;
  width: 0;
  height: 0;
  top: 50%;
  right: 50%;
  margin-right: -3px;
  border: 3px solid transparent;
  margin-top: -4px;
}
.c-input__ctrl-inc .triangle {
  border-bottom: 4px solid #8492A6;
}
.c-input__ctrl-dec .triangle {
  border-top: 4px solid #8492A6;
}



